        <h1>About</h1>
        <p>This application allows the user to connect with spotify, and turn a playlist into a set of bingo cards.</p>
        <p>This application runs entirely within the users browser on the users computer. No data whatshowever will be sent by this website to a central server. The reason one must agree to share name, profile picture and followers is because this a requirement from Spotify itself.</p>
        <p>It is recommended to use a playlist of at least 25 tracks, and at most 50 tracks.</p>
        <p>To disconnect this app from your spotify-account, simply go to <a href="https://spotify.com/account/apps">spotify.com/account/apps</a></p>
